@import "../../variables";

.modal {
    .modal-content {
        .input.f {
            margin-top: 20px;
        }

        .input::-webkit-outer-spin-button,
        .input::-webkit-inner-spin-button {
            -webkit-appearance: none;
            margin: 0;
        }

        .input[type=number] {
            -moz-appearance: textfield;
        }
    }
}